html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@charset "UTF-8";
* {
  box-sizing: border-box; }

body {
  background-color: white; }

.widget__header {
  display: flex;
  flex-direction: row;
  background: #161616; }

nav {
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-direction: row;
  background: #161616; }
  nav ul.widget__menubar {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
    nav ul.widget__menubar .submenu-item--expanded ul.widget__menubar {
      box-shadow: 0 4px 2px -2px gray; }
    nav ul.widget__menubar .widget__menubar-item li.all_products a {
      font-size: 20px;
      color: #1a73e8;
      margin: 5% 0 0 8%; }
      nav ul.widget__menubar .widget__menubar-item li.all_products a:hover {
        color: #212121; }
  nav li {
    list-style: none;
    box-sizing: border-box; }
    nav li:hover > ul.widget__menubar {
      display: block; }
    nav li a {
      display: block;
      padding: 1rem 1.5rem 0 1.5rem;
      text-decoration: none;
      color: white;
      width: 100%;
      box-sizing: border-box;
      position: relative; }

.widget__menubar-item img {
  width: 35%;
  height: 35%;
  max-width: 200px;
  max-height: 200px;
  margin: 5% 10% 0 10%; }

.widget__menubar-item:hover > a {
  color: white; }

.widget__menubar-item.submenu-item--expanded:hover .widget__menubar {
  display: flex;
  flex: 100% 1; }

.widget__menubar-item.submenu-item--expanded:hover > a::after {
  visibility: visible; }

.widget__menubar-item.submenu-item--expanded > a {
  padding: 1rem 2.5rem 1rem 1.5rem; }
  .widget__menubar-item.submenu-item--expanded > a:before {
    content: "";
    position: absolute;
    border-left: 0.25rem solid transparent;
    border-bottom: 0.25rem solid transparent;
    border-right: 0.25rem solid transparent;
    right: 1.25rem;
    bottom: calc(50% - 0.425rem);
    z-index: 0;
    border-top: 0.25rem solid #fff; }
  .widget__menubar-item.submenu-item--expanded > a:after {
    visibility: hidden;
    content: "";
    position: absolute;
    bottom: 0;
    z-index: 5; }

.widget__menubar-item.submenu-item--expanded .widget__menubar {
  border-top: 3px solid #ffb400;
  display: none;
  position: absolute;
  left: 0;
  width: 100%;
  background: white;
  z-index: 1; }
  .widget__menubar-item.submenu-item--expanded .widget__menubar .widget__menubar-item {
    width: 100%; }

.widget__menubar li.widget__main-menu--hover:not(:first-child):hover {
  border-bottom: 0.2rem solid #ffb400; }

.menu__items {
  position: relative;
  margin: 5% 0 5% 0; }
  .menu__items .main-menu__title a {
    font-size: 17px;
    color: #1a73e8; }
    .menu__items .main-menu__title a:hover {
      color: #212121; }
  .menu__items .sub-menu__title {
    flex-wrap: wrap;
    margin: 0; }
    .menu__items .sub-menu__title a {
      padding: 0.05rem 0.01rem 0 1.5rem !important;
      width: 100%;
      margin-right: 2px !important;
      font-size: 14px;
      color: #5f6368; }
      .menu__items .sub-menu__title a:hover {
        color: #6d9bd6 !important; }
      .menu__items .sub-menu__title a:after {
        content: ' \BB'; }

.menu__items::after {
  content: '';
  height: 100%;
  width: 1px;
  position: absolute;
  right: 0;
  top: 0;
  background-color: gainsboro; }

.widget__logo {
  font-family: 'Kaushan Script', cursive;
  margin: auto;
  padding: 0.50rem 1.5rem 0 1.5rem;
  color: white;
  text-decoration: none;
  font-size: 1.9rem; }

.multi-level, .side-drawer__item ul, .widget__mobile input[type="checkbox"] {
  display: none; }

#burger-menu:checked ~ .multi-level, .side-drawer__item input:checked ~ ul {
  display: block; }

label:hover {
  cursor: pointer; }

label {
  width: 100%;
  display: block;
  z-index: 3;
  position: relative;
  color: #fff; }

.side-drawer__item {
  background-color: #161616;
  border-bottom: 1px solid gainsboro; }
  .side-drawer__item .side-drawer__sub-item {
    background-color: #161616;
    border-bottom: 1px solid gainsboro; }
    .side-drawer__item .side-drawer__sub-item li {
      border-bottom: 1px solid gainsboro; }
    .side-drawer__item .side-drawer__sub-item > li::after {
      background-color: palegoldenrod; }

#burger-menu__icon {
  font-size: 28px;
  line-height: 50px;
  padding-left: 1em;
  color: white; }

.widget__mobile ul, .widget__mobile li, label {
  line-height: 50px;
  margin: 0;
  padding: 0 0.75em;
  list-style: none;
  text-decoration: none;
  font-weight: 100;
  width: 100%; }

.widget__mobile li {
  margin: 0; }
  .widget__mobile li a {
    line-height: 50px;
    margin: 0;
    list-style: none;
    text-decoration: none;
    font-weight: 100;
    color: #fff;
    cursor: pointer; }
    .widget__mobile li a:hover {
      color: #fff; }

.side-drawer {
  margin-top: 50px;
  height: 100%;
  overflow: auto;
  background: #161616;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 50%;
  min-width: 250px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  -o-transition: transform 0.3s ease-out;
  -ms-transition: transform 0.3s ease-out;
  -webkit-transition: transform 0.3s ease-out;
  -moz-transition: transform 0.3s ease-out; }
  .side-drawer .side-drawer__item ul {
    padding: 0 0.25em; }
  .side-drawer.open {
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -o-transform: translateX(0);
    -ms-transform: translateX(0); }

@media only screen and (min-width: 768px) and (max-width: 992px) {
  .widget__menubar-item img {
    width: 50%;
    height: 50%;
    max-width: 250px;
    max-height: 250px;
    margin: 5% 10% 0 10%; } }

@media only screen and (max-width: 500px) {
  .side-drawer {
    width: 65%; } }

@media only screen and (max-width: 767px) {
  .side-drawer {
    width: 45%; }
  .widget__mobile .side-drawer__item label {
    font-size: 20px; }
  .widget__mobile li a {
    padding: 0 1em; }
  nav ul.widget__menubar {
    display: none; }
  nav > .widget__menubar > li.submenu-item--expanded > a {
    pointer-events: none; }
  nav .widget__menubar-item img {
    display: none; }
  nav .widget__navigation-items {
    display: none; } }

@media only screen and (min-width: 768px) {
  .side-drawer {
    display: none; }
  .widget__toggle-button {
    display: none; } }

